.scrolldown-icon-container {
  position: absolute;
  top: calc(100vh - 120px);
  z-index: 30;
  right: 20px;
  width: 24px;
  height: 24px;
}

@media only screen and (max-width: 600px) {
  .scrolldown-icon-container {
    top: calc(100vh - 190px);
  }
}

.scrolldown-icon-chevron {
  position: absolute;
  width: 28px;
  height: 8px;
  opacity: 0;
  transform: scale3d(0.5, 0.5, 0.5);
  animation: move 3s ease-out infinite;
}

.scrolldown-icon-chevron:first-child {
  animation: move 3s ease-out 1s infinite;
}

.scrolldown-icon-chevron:nth-child(2) {
  animation: move 3s ease-out 2s infinite;
}

.scrolldown-icon-chevron:before,
.scrolldown-icon-chevron:after {
  content: ' ';
  position: absolute;
  top: 0;
  height: 100%;
  width: 51%;
  background: #5E6572;
}

.scrolldown-icon-chevron:before {
  left: 0;
  transform: skew(0deg, 30deg);
}

.scrolldown-icon-chevron:after {
  right: 0;
  width: 50%;
  transform: skew(0deg, -30deg);
}

@keyframes move {
  25% {
    opacity: 1;

  }
  33% {
    opacity: 1;
    transform: translateY(30px);
  }
  67% {
    opacity: 1;
    transform: translateY(40px);
  }
  100% {
    opacity: 0;
    transform: translateY(55px) scale3d(0.5, 0.5, 0.5);
  }
}

.scrolldown-icon-text {
  display: block;
  text-align: right;
  margin-top: 65px;
  margin-left: -14px;
  font-size: 12px;
  color: rgb(124, 124, 124);
  text-transform: uppercase;
  white-space: nowrap;
  opacity: .25;
  animation: pulse 2s linear alternate infinite;
}

@keyframes pulse {
  to {
    opacity: 1;
  }
}